export default {
  props: {
    second: {
      type: Number,
      default: 60
    }
  },
  data() {
    return {
      time: 0,
      disabled: true
    }
  },
  methods: {
    run() {
      this.$emit('run');
    },
    start() {
      this.disabled = false;
      this.time = this.second;
      this.timer();
    },
    stop() {
      this.time = 0;
    },
    timer() {
      if(this.time > 0) {
        this.time--;
        setTimeout(this.timer,1000);
      }else{
        this.disabled = true;
      }
    }
  },
  computed: {
    getTips() {
      return this.time > 0 ? `${this.time}s后再获取`:`获取验证码`;
    }
  }
}



