import header from '@/components/header';
import button from '@/components/button';
import timerBtn from '@/components/timerBtn';
import { Toast } from 'mint-ui';
export default {
  data() {
    return {
      titleContent: '我的宝宝',
      phoneNumber: '',
      code: '',
      showFlag: true
    }
  },
  methods: {
    // 获取手机验证码
    sendCode() {
        // 验证手机号码格式
        if (this.validatorPhoneNumber(this.phoneNumber)) {
          //发送手机验证码
          // this.$http.post(``).then((response) => {
          //
          // });
          this.$refs.timerbtn.start(); //启动倒计时
          this.getTost('验证码已发送，请查收');
        } else {
          this.getTost('手机号码的格式有误');
        }
    },
    goVerify() {
      console.log("1111");
      // 验证手机验证码
      // this.$http.post().then((response) => {
      //
      // });
    },
    // tost提示
    getTost(content){
      let instance = Toast(content);
      setTimeout(() => {
        instance.close();
      }, 2000);
    },
    // 验证手机号码格式
    validatorPhoneNumber(phoneNumber){
      return /^[0-9]{11}$/.test(phoneNumber);
    }
  },
  components: {
    'v-header': header,
    'v-button': button,
    'timer-btn': timerBtn
  }
}



